.chefs {
  &__title {
    position: relative;
    margin: 0 auto;
    margin-bottom: 80px;
    &::before,
    &::after {
      position: absolute;
      bottom: 0;
      content: '';
      width: 352px;
      height: 2px;
      background-color: var(--color-secondary-light);
    }
    &--color {
      color: var(--color-primary-accent);
    }
    &::before {
      left: 0;
    }
    &::after {
      right: 0;
    }
  }
}

.chefs-list {
  @include centeredFlex();
  gap: 30px;
  @media screen and (max-width: $mobile) {
    display: block;
  }
  &__item {
    @include centeredFlex($align: flex-start);
    gap: 30px;
  }
  &__img {
    width: 268px;
    height: 445px;
    border-radius: 4px 4px 4px 200px;
    box-shadow: -16px 16px 0 0 var(--color-primary-light),
      -16px 16px 0 2px var(--color-secondary-dark);
    @media screen and (min-width: $mobile) {
      width: 156px;
      height: 258px;
    }
  }
  &__article {
    width: 334px;
  }
  &__title {
    margin-bottom: 16px;
    @include text($size: 40px, $font-weight: 600);
  }
  &__text {
    margin-bottom: 20px;
    @media screen and (max-width: $mobile) {
      width: 162px;
    }
  }
}
