.traditions {
  padding: 0;

  &__container {
    padding: 0 100px;
    display: flex;
    gap: 104px;
    @media screen and (max-width: $mobile) {
      display: block;
      padding: 0;
    }
  }
  &__part {
    margin-top: 96px;
    width: 710px;
    display: flex;
    flex-direction: column;
    gap: 42px;
    @media screen and (max-width: $mobile) {
      width: 322px;
    }
  }
  &__title {
    text-align: start;
  }
  &__text {
    width: 600px;
    @media screen and (max-width: $mobile) {
      width: 322px;
    }
    &--width {
      width: 350px;
    }
    &--transform {
      @include text($font-weight: 600, $color: var(--color-primary-accent));
      text-transform: uppercase;
    }
    &--color {
      @include text($font-weight: 600, $color: var(--color-primary-accent));
    }
  }
  &__image {
    margin-top: 96px;
    max-width: 393px;
    max-height: 466px;
    border-radius: 400px 400px 0px 0px;
    @media screen and (max-width: $mobile) {
      display: none;
    }
  }
}
